<template>
  <div class="centerLeft chartBox">
    <div class="common_title">
      项目实时运行功率<span>2021-11-28 12:00:00</span>
    </div>
    <div class="item">
      <ul>
        <li>项目实时运行功率 <span>2.55 MV</span></li>
        <li style="text-align: right">本日最大发电功率 <span>5.55 MV</span></li>
      </ul>
    </div>
    <div id="myEchart"></div>
  </div>
</template>

<script>
export default {
  name: "lineChart",
  data() {
    return {
      option: {},
    };
  },
  methods: {
    createData() {
      var chartDom = document.getElementById("myEchart");
      var myChart = this.$echarts.init(chartDom);
      var option;
      option = {
        title: {
          text: "本日功率曲线",
          left: "center",
          textStyle: {
            color: "#fff",
            fontWeight: "normal",
          },
        },
        tooltip: {
          trigger: "axis",
        },
        grid: {
          top: "15%",
          left: "8%",
          right: "3%",
          bottom: "15%",
        },
        xAxis: {
          data: [
            "00:00",
            "01:00",
            "02:00",
            "03:00",
            "04:00",
            "05:00",
            "06:00",
            "07:00",
            "08:00",
            "09:00",
            "10:00",
            "11:00",
            "12:00",
            "13:00",
            "14:00",
            "15:00",
            "16:00",
            "17:00",
            "18:00",
            "19:00",
            "20:00",
            "21:00",
            "22:00",
            "23:00",
          ],
          boundaryGap: false,
          axisLine: {
            //坐标轴颜色
            show: true,
            lineStyle: {
              color: "#5b73ffb0",
            },
          },
          axisLabel: {
            //坐标轴上文字颜色
            interval: 1,
            color: "#fff",
          },
          axisTick: {
            //坐标轴刻度线
            show: false,
          },
        },
        yAxis: {
          name: "(MV)",
          max: function (value) {
            return value.max * 1.2;
          },
          nameTextStyle: {
            color: "#fff",
            fontSize: 12,
          },
          type: "value",
          splitLine: {
            show: false, //去掉网格线
          },

          axisLine: {
            //坐标轴颜色
            show: true,
            lineStyle: {
              color: "#5b73ffb0",
            },
          },
          axisLabel: {
            //坐标轴上文字颜色
            color: "#fff",
          },
        },
        series: [
          {
            data: [
              0.5, 0.6, 1.8, 2.4, 1.3, 3.5, 2.8, 0.7, 2.6, 2.8, 3.4, 2.3, 1.5,
              2.8, 3.1, 2.1, 2.6, 2.8, 3.4, 2.3, 1.5, 2.8, 3.1, 2.1,
            ],
            smooth: true,
            type: "line",
            lineStyle: {
              width: 1,
              color: "#d6f478",
            },
            itemStyle: {
              normal: {
                color: "#d6f478",
              },
            },
          },
        ],
      };

      option && myChart.setOption(option);
    },
  },
  mounted() {
    this.createData();
  },
};
</script>

<style lang="less" scoped>
#myEchart {
  width: 100%;
  height: 100%;
  max-height: 200px;
}
.centerLeft {
  width: 32%;
}
</style>
