<template>
  <div class="centerChart chartBox">
    <div class="common_title">项目实时运行情况</div>

    <div class="chartList common_flex">
      <div class="state">
        <div>EMS状态</div>
        <ul>
          <li>待机<span class="color_0"></span></li>
          <li>启动<span class="color_1"></span></li>
          <li>故障<span class="color_2"></span></li>
          <li>停机<span class="color_3"></span></li>
        </ul>
      </div>
      <div class="list_item">
        <div class="item_title">可充功率（KW）</div>
        <div id="AC"></div>
      </div>
      <div class="list_item">
        <div class="item_title">可放功率（KW）</div>
        <div id="DC"></div>
      </div>
    </div>

    <div class="percent">
      <span>SOC</span>
      <dv-percent-pond :config="config" style="width: 80px; height: 40px" />
    </div>
  </div>
</template>

<script>
export default {
  name: "centerChart",
  data() {
    return {
      option: {},
      AC: 7.42,
      DC: 9.12,
      config: {
        value: 80.22,
        fontSize: 12,
        colors: ["#01c4f9", "#c135ff"],
        lineDash: [10, 2],
      },
    };
  },
  methods: {
    createData(id, value) {
      var chartDom = document.getElementById(id);
      var myChart = this.$echarts.init(chartDom);
      var option;
      option = {
        series: [
          {
            type: "gauge",
            min: 0,
            max: 60,
            splitNumber: 6,
            itemStyle: {
              //选中和指针颜色
              color: "#f1b5b5",
            },
            radius: "95%",
            progress: {
              show: true,
              width: 10,
            },
            axisLine: {
              //圆圈样式
              lineStyle: {
                width: 10,
                color: [[1, "#6da7ed"]],
              },
            },
            pointer: {
              //指针长度
              length: "50%",
            },
            axisTick: {
              show: false,
            },
            splitLine: {
              //刻度线
              length: 8,
              distance: 3,
              lineStyle: {
                width: 2,
                color: "#fff",
              },
            },
            axisLabel: {
              //刻度颜色
              distance: 15,
              color: "#fff",
              fontSize: 14,
            },
            anchor: {
              show: true,
              showAbove: true,
              size: 5,
              itemStyle: {
                borderWidth: 10,
              },
            },
            title: {
              show: false,
            },
            detail: {
              valueAnimation: true,
              fontSize: 26,
              offsetCenter: [0, "70%"],
              color: "#fff",
            },
            data: [
              {
                value: value,
              },
            ],
          },
        ],
      };

      option && myChart.setOption(option);
    },
  },
  mounted() {
    this.createData("AC", this.AC);
    this.createData("DC", this.DC);
  },
};
</script>

<style lang="less" scoped>
.centerChart {
  position: relative;
  width: 50%;
}
.chartList {
  height: calc(100% - 51px);
  .state {
    width: 22%;
    padding-left: 30px;
    box-sizing: border-box;
    margin-top: 15px;
    color: #d3adf7;
    ul {
      margin-top: 10px;
      color: #bbb;
      li {
        height: 36px;
        line-height: 36px;
        span {
          width: 14px;
          height: 14px;
          background: #cccccc;
          display: inline-block;
          margin-left: 10px;
          border-radius: 50%;
          margin-top: 2px;
        }
        .color_0 {
          background: #cccccc;
        }
        .color_1 {
          background: #83ea93;
        }
        .color_2 {
          background: #d65240;
        }
        .color_3 {
          background: #666;
        }
      }
    }
  }
  .list_item {
    margin-top: -10px;
    flex: 1;
    width: 50%;
    height: 100%;
    .item_title {
      text-align: center;
      color: #a8cefe;
      margin-bottom: 10px;
    }
    #AC,
    #DC {
      width: 100%;
      min-height: 180px;
      height: calc(100% - 15px);
    }
  }
}
.dv-percent-pond {
  margin-top: 10px;
}
/deep/ .dv-percent-pond text {
  font-size: 18px;
  text-anchor: middle;
  font-weight: normal;
}
.percent {
  text-align:center;
  position: absolute;
  right: 50%;
  margin-right: -123px;
  top: 30px;
}
</style>
